@import url(https://fonts.googleapis.com/css?family=Antic&display=swap);

* {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  font-family: 'Antic', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#000000), to(#434343));
  background: -webkit-linear-gradient(left, #000000, #434343);
  background: linear-gradient(to right, #000000, #434343);
  width: 100%;

  min-height: 100vh;
}
header {
  background: #070608;
  display: flex;
  align-items: center;
  padding: .5rem 0;
  text-align: center;
}

.header-logo {
  flex: 1 1;
  color: #c9c9c9;
  font-size: 2rem;
}

.header-search {
  flex: 2 1;
  display: flex;
}

@media (max-width:768px) {
  .header-search {
    flex: 3 1;
  }
}

.header-search-icon {
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-items: center;
  background: #262626;
  padding: 0 0rem 0 .5rem;
  width: 10%;

}

.header-search i {
  color: #7e7c79;
  cursor: pointer;
}

.header-search input {
  background: #262626;
  border: none;
  outline: none;
  height: 2.5rem;
  width: 90%;
  color: #7e7c79;
  font-size: 1rem;
  text-indent: .5rem;
  font-family: 'Antic', sans-serif;
}

@media (max-width:600px) {
  .header-search input {
    font-size: .9rem;
  }
}

.header-user {
  flex: 1 1;

}

.header-user img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.movies-container {
  width: 1200px;
  max-width: 95%;
  margin: 2rem auto 2rem auto;
  display: flex;
  flex-wrap: wrap;
}

.movie-card {
  margin: .5rem auto .5rem auto;
  cursor: pointer;
  -webkit-filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.5));
  position: relative;
}

.movie-rating {
  position: absolute;
  top: .5rem;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  padding: .3rem;

}

.movie-rating-star i {
  color: #f2b01e;
  margin: 0 .5rem 0 .2rem;
}

.movie-rating span:last-of-type {
  color: #fff;
  font-weight: bold;
  padding-right: .2rem;

}
.movieDetails {
  width: 780px;
  max-width: 95%;
  margin: 3rem auto 3rem auto;
  background: #070608;
  padding-bottom: .5rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.8);
}

.current-poster img {
  max-width: 100%;
}

.current-title {
  margin: .5rem 0;
}

.current-details {
  margin: 0 .5rem;
}

.current-title h2 {
  color: #fff;
}

.current-type_rating {
  display: flex;
  justify-content: space-between;

  margin: .5rem 0;
}

.current-type {
  color: #c9c9c9;
}

.current-movie-avg {
  color: #c9c9c9;
}

.current-overview {
  color: #c9c9c9;
  margin: 1rem 0;
}

.current-date {
  color: #c9c9c9;
}

.current-popularity {
  color: #c9c9c9;
  margin: .5rem 0;
}

.goto-options a {
  text-decoration: none;
  font-family: 'Antic', sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#232526), to(#414345));
  background: -webkit-linear-gradient(left, #232526, #414345);
  background: linear-gradient(to right, #232526, #414345);
  padding: .5rem 1rem;
  border-radius: 2rem;
  color: #fff;
  text-transform: uppercase;

  width: 150px;
  text-align: center;

  display: inline-block;

}

.goto-options a:active {

  background: -webkit-gradient(linear, left top, right top, from(#000000), to(#434343));

  background: -webkit-linear-gradient(left, #000000, #434343);

  background: linear-gradient(to right, #000000, #434343);
  color: #fff;

}

.goto-options {
  display: flex;
  justify-content: space-around;
  margin: 1rem 0;
}

.currentMovie-avg {
  color: #c9c9c9;
}
